$bg: rgba(0, 0, 0, 0.2);

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.overlay {
  text-align: center;
}

img,
.overlay {
  transition: .3s all;
  border-radius: 20px;
}

.bg {
  width: 165px;
  position: relative;
  
}


.ARButton {
  border-radius: 15px;
  background: var(--blue, #0A84FF);
  /* System Ultra Thin Material */
  z-index: 999999999;
  backdrop-filter: blur(16px);
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  width: 90%;
  height: 50px;
  align-self: center;


  color: var(--white, #FFF);
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.56px;
}

