@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap');

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

.selectors{
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  overflow-x: hidden;
  overflow-y: scroll;
  font-family: 'Titillium Web', sans-serif;
  border-radius: 25px;
  margin: 5px;
  box-shadow: 0px 0px 10px #00000079;
  gap: 1.3vh;
  padding-bottom: 3vh;
  
}

.domSelector {
  width: 50vw;
  max-width: 600px;
  min-width: 200px;


  /* height: calc(70vh + 30vw); */
  /* min-height: calc(50 * 3); */

  height: 70vh;

  position: fixed;

  /* top:50%;
  left:20px; */
  top: 0px;
  left: 0px;
  z-index: 99999;

  transform: scale(1);

  background-color: #39393946;
  backdrop-filter: blur(33px);
  -webkit-backdrop-filter: blur(33px);
  color: #ffffff;
}

.domSelectorBottomTab {
  width: 100vw;
  position: fixed;
  height: 7vh;

  background-color: #39393946;
  backdrop-filter: blur(33px);
  -webkit-backdrop-filter: blur(33px);
  /* color: #0000007a; */

  bottom: 0px;
  z-index: 1;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  color: white;
}

.BottomTabButton {
  height: 100%;
  width: 20vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-radius: 50px;

  gap: 0.6vh;


  font-size: 10px;
  z-index: 1;

  fill: white;

  transition: inherit;
}

.BottomTabButton * {
  z-index: 0;
  pointer-events: none;
}

.BottomTabButton svg{
  width: 100%;
  height: 32%;
}

.touchableFeedback {
  transition: 
    background-color 0.5s cubic-bezier(0,1.05,.75,1.01);

  background-color: #ffffff00;
}

.touchableFeedback:active {

  background-color: #ffffffad;
}




.iconC {
  color: #ffffff;
}
.domSelector2 {
  width: 50%;
  height: 50%;
  position: fixed;

  /* right: 20px;
  top:50%; */
  bottom: 0px;
  right: 0px;
  z-index: 99999;

  transform: scale(1);

}


.hidden {
  width: 0.001px;
  height: 0.001px;
  transform: scale(0.00001) translateX(0.0001px) !important;
}


.hiddenRight {
  /* width: 0.001px;
  height: 0.001px; */
  /* transform: translateX(90px); */
  right: -90px!important;

}

.hiddenBottom {
  bottom: -90px!important;
}


.ballerAnim {
  transition: 
    width 0.7s cubic-bezier(.05,.18,.17,.96),
    opacity 0.7s cubic-bezier(.05,.18,.17,.96),
    height 0.7s cubic-bezier(.05,.18,.17,.96),
    transform 0.7s cubic-bezier(.05,.18,.17,.96),
    right 0.7s cubic-bezier(.05,.18,.17,.96),
    bottom 0.7s cubic-bezier(.05,.18,.17,.96);
  
  transform: translateX(0.0001px);
}

.ballerAnim> .row{
  transition: 
    opacity 0.7s cubic-bezier(1,.13,.54,1);
  opacity: 1;
}

.ballerAnim.hidden> .row{
  transform:rotateX('90deg');
  opacity: 0.001;
}

.row{
  display: flex;
  justify-content: center;
  padding: 0px 2vw;
}

h1 {
  opacity: 0.7;
  letter-spacing: 1px;
  /* font-size: calc(2vw * 1.2vh); */
  font-size: min(7vw, 5vh);
  font-weight: 600;
}

h2 {
  opacity: 0.5;
  font-size: min(4vw, 3vh);
  font-weight: 300;
  font-style: italic;
}

article {
  opacity: 0.35;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.visitSite {
  background-color: #00000096;
  color: #0a84ffcb;
  backdrop-filter: blur(33px);
  -webkit-backdrop-filter: blur(33px);
  padding: 0.6vh 20%;
  font-size: 20px;
  border-radius: 15px;
  text-decoration: none;
  font-weight: 600;
}



#modelViewer {
  height: 20vh;
}

.backgroundBlur {
  backdrop-filter: blur(80px);
  -webkit-backdrop-filter: blur(80px);
  transition: backdrop-filter 0.5s, opacity 0.5s;
}



.domSelectorButton {

  position: fixed;
  top:2%;
  right:20px;
  z-index: 9999999;
  /* transition: width 0.2s ease-out, height 0.2s ease-out; */

}
.domSelectorButtonRightMid {

  position: fixed;
  top:50%;
  right:20px;
  z-index: 9999999;
  transition: width 0.2s ease-out, height 0.2s ease-out;

}
.domSelectorButton2 {
  width: 50px;
  height: 50px;
  position: fixed;
  right: 20px;
  top:50%;
  z-index: 99999;

  transition: width 0.2s ease-out, height 0.2s ease-out;
  background-color: red;

  overflow-x: hidden;
}



.examineMode#goBackBtn {
  pointer-events: none;
  opacity: 0.3;
}

.examineMode#examineBtn {
  fill: #0A84FF;
  color: #0A84FF;
  background-color: #000;
}

.examineMode.domSelector {
  height: 20vh;
}