:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;
  /* background-image: url(regular.png); */

  background-repeat: no-repeat;
  background-size: cover;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.image {
  width: 101.619px;
  height: 60px;
  position: absolute;

}

.titleText {
  color: var(--white, #FFF);
  text-align: center;
  /* font-family: SF Pro Display; */
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.56px;
}
.background {
  position: fixed;
  width: 100%;
  height: 150%;
  top:0;
  z-index: -1;
  left: 0;
}

.subTitleText {
  color: var(--white, #FFF);
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.44px;
  opacity: 0.60;
}

.wrapper {
  flex-direction: row;
  width: 100%;
  z-index: 999;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}


.threeDModelButton {
  margin:10px;
  width: 163px;
  height: 160px;
  border-radius: 15px;
  background: var(--i-os-blur-system-ultra-thin-material, rgba(57, 57, 57, 0.60));
  backdrop-filter: blur(16px);
  /* flex-direction: row; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}
input[type="radio"] {
  -webkit-appearance: none;
  appearance: none;
  /* For iOS < 15 to remove gradient background */
  background-color: #fff;
  /* Not removed via appearance */
  margin: 0;
}

.ARButton {
  border-radius: 15px;
  background: var(--blue, #0A84FF);
  /* System Ultra Thin Material */
  backdrop-filter: blur(16px);
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  bottom:20px;
  width: 90%;
  height: 50px;
  align-self: center;
}

.ARButton text {
  color: var(--white, #FFF);
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.56px;
}

.topButton {
  border-radius: 15px;
background: var(--black, #1C1C1E);
/* System Ultra Thin Material */
backdrop-filter: blur(16px);
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  top:20px;
  width: 150px;
  height: 50px;
  align-self: center;
}

.topButton text {
  color: var(--white, #fff);
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.56px;
}

.topLeftButton {
  border-radius: 15px;
background: var(--black, #1C1C1E);
/* System Ultra Thin Material */
backdrop-filter: blur(16px);
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  top:20px;
  left:20px;
  width: 60px;
  height: 50px;
  align-self: center;
}

.topLeftButton text {
  color: var(--blue, #fff);
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.56px;
}

button:hover {
  border-color: #646cff;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}


.radio-button {
  /* display: inline-block;
  position: relative;
  
  border-radius: 5px;
  margin: 10px;
