  transition: filter 0.3s; 
  position: relative;
  width: 400px;
  height: 300px;

}

.radio-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: blur(0); /* Initial blur set to 0 (no blur) */
  z-index: -1;
  transition: filter 0.3s; /* Smooth transition for the filter property */
}
/* :hover {
  filter: blur(5px); 
} */

.radio-button:hover {
  filter: blur(5px); /* Adjust the blur intensity as needed */
}


.radio-button-selected {
  display: inline-block;
  position: relative;
  border: 2px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  cursor: pointer;
  margin:10px;
  width: 163px;
  height: 160px;
  border-radius: 15px;
  background: var(--i-os-blur-system-ultra-thin-material, rgba(57, 57, 57, 0.60));
  backdrop-filter: blur(16px);
  /* flex-direction: row; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.content {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  color: white;
  text-align: center;
}


.radio-button input[type="radio"]:checked {
  background-color: red;
}

.radio-button input[type="radio"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.radio-button label {
  display: block;
  padding: 5px 10px;
}

.radio-button input[type="radio"]:checked + label {
  background-color: #f0f0f0;
  border-color: #007bff;
  color: #007bff;
}